
// 
// sweetalert.scss
//

.swal2-modal {
  font-family: $font-family-base;
  box-shadow: 0 10px 33px rgba(0,0,0,.1);

  .swal2-title {
    font-size: 24px;
  }
  .swal2-content {
    font-size: 16px;
  }
  .swal2-spacer {
    margin: 10px 0;
  }
  .swal2-file, .swal2-input, .swal2-textarea {
    border: 2px solid $gray-300;
    font-size: 16px;
    box-shadow: none;
  }

  .swal2-styled{
    &:focus {
      box-shadow: none !important;
    }
  }
}

.swal2-icon{
  &.swal2-question {
    color: $primary;
    border-color: $primary;
  }
  &.swal2-warning {
    color: $warning;
    border-color: $warning;
  }

  &.swal2-error {
    border-color: $danger;
    .line {
      background-color: $danger;
    }
  }

  &.swal2-info {
    color: $info;
    border-color: $info;
    .line {
      background-color: $info;
    }
  }
}

.swal2-modal{
  .swal2-file, .swal2-input, .swal2-textarea {
    &:focus {
      outline: 0;
      border: 2px solid $primary;
    }
  }
}
